package ui.screens.adminFeatures.newQuestion

import androidx.compose.runtime.*
import app.softwork.routingcompose.Parameters
import app.softwork.routingcompose.Router
import kotlinx.coroutines.launch
import network.model.ToastModel
import network.model.ToastType
import org.jetbrains.compose.web.attributes.*
import org.jetbrains.compose.web.css.height
import org.jetbrains.compose.web.css.px
import org.jetbrains.compose.web.dom.*
import repository.QuestionRepository
import repository.UserRepository
import ui.screens.adminFeatures.questionDetails.model.isNotEmpty
import util.headerComponent
import util.titleView
import util.toastView

@Composable
fun newQuestionScreen(
    userRepository: UserRepository,
    questionRepository: QuestionRepository,
    parameters: Parameters?,
    router: Router
) {
    val coroutineScope = rememberCoroutineScope()
    var questionName by remember { mutableStateOf("") }
    var imageFile by remember { mutableStateOf("") }
    var phoneNumberFile by remember { mutableStateOf("") }
    var isSendingData by remember { mutableStateOf(false) }
    var toastModel by remember { mutableStateOf<ToastModel?>(null) }
    coroutineScope.launch {
        parameters?.let { console.log(it.raw) }
        val questionErrorModel = questionRepository.getSendingQuestionError()
        if (questionErrorModel.isNotEmpty()) {
            toastModel = ToastModel(questionErrorModel.title, questionErrorModel.message, ToastType.ERROR)
            questionRepository.deleteQuestionError()
            isSendingData = false
        }
    }


    headerComponent(userRepository, router)

    Div({ classes("container-fluid") }) {
        titleView("New Question")
        Form(attrs = {
            id("newQuestionForm")
            encType(FormEncType.MultipartFormData)
            classes("needs-validation")
            addEventListener("submit") {
                it.preventDefault()
            }
        }) {

            Div({ classes("row", "d-flex", "justify-content-center", "mt-5") }) {
                Div({ classes("col-md-4", "col-lg-4", "col-sm-6", "col-xl-4") }) {
                    Input(type = InputType.Text, attrs = {
                        id("questionName")
                        name("questionName")
                        classes("form-control")
                        onInput {
                            questionName = it.value
                        }
                        placeholder("Question Name *")
                        required(true)
                    }
                    )
                }
            }

            Div({ classes("row", "d-flex", "justify-content-center", "mt-4") }) {
                Div({ classes("col-md-4", "col-lg-4", "col-sm-6", "col-xl-4") }) {
                    Label(forId = "imageFile") { Text("Image links") }
                    Input(type = InputType.File, attrs = {
                        id("imageFile")
                        accept(".csv")
                        name("imageFile")
                        classes("form-control")
                        required(true)
                        onInput {
                            imageFile = it.value
                        }
                    })
                }
            }

            Div({ classes("row", "d-flex", "justify-content-center", "mt-3") }) {
                Div({ classes("col-md-4", "col-lg-4", "col-sm-6", "col-xl-4") }) {
                    Label(forId = "phoneNumberFile") { Text("User Phone numbers") }
                    Input(type = InputType.File, attrs = {
                        id("phoneNumberFile")
                        accept(".csv")
                        name("phoneNumberFile")
                        classes("form-control")
                        required(true)
                        onInput {
                            phoneNumberFile = it.value
                        }
                    })
                }
            }

            Div({ classes("row", "d-flex", "justify-content-center", "mt-5") }) {
                Div({ classes("col-md-3", "col-lg-3", "col-sm-6", "col-xl-3") }) {
                    Button(attrs = {
                        classes("btn", "btn-dark", "btn-block", "rounded-pill", "col-12")
                        style { height(50.px) }
                        type(ButtonType.Submit)
                        if (isSendingData) {
                            disabled()
                        }
                        onClick {
                            if (allFileAreValid(questionName, imageFile, phoneNumberFile)) {
                                isSendingData = true
                                runJsCodeToCreateQuestion()
                            }
                        }
                    }
                    ) {
                        if (isSendingData) {
                            Span(attrs = {
                                attr("aria-hidden", "true")
                                attr("role", "status")
                                classes("spinner-border", "spinner-border-sm")
                            })
                            Span(attrs = {
                                attr("aria-hidden", "true")
                                attr("role", "status")
                                classes("r-only", "spinner-border-sm")
                            }) {
                                Text("  Submitting, Please wait...")
                            }

                        } else {
                            Text("Create question")
                        }
                    }
                }
            }
        }
    }

    if (toastModel != null) {
        toastView(toastModel!!.title, toastModel!!.description, toastModel!!.type){
            toastModel=null
        }
    }
}

private fun allFileAreValid(questionName: String, imageFile: String, phoneNumberFile: String): Boolean {
    return questionName.isNotEmpty() && imageFile.isNotEmpty() && phoneNumberFile.isNotEmpty()
}

private fun runJsCodeToCreateQuestion() {
    js(
        "var formData = new FormData(\$(\"#newQuestionForm\")[0]);\n" +
                " \$.ajax({ url: apiURL+'/question',\n" +
                "     type: 'POST',data: formData,\n" +
                "     async: true,cache: false,contentType: false,\n" +
                "     enctype: 'multipart/form-data', processData: false,\n" +
                "     success: function (response) {\n" +
                "        saveQuestion(response);\n" +
                "        window.location.replace(baseUrl+'/#/admin/question-details');\n" +
                "     }, error:function (error){" +
                "        console.log(error);" +
                "        var responseJSON=error.responseJSON;" +
                "        saveQuestionError(responseJSON);" +
                "        var time = new Date().getTime();" +
                "        window.location.replace(baseUrl+'/#/admin/new-question?error='+ responseJSON.error +'&time='+time);\n" +
                "    }\n" +
                "  });"
    )
}
